body {
  margin: 0;
  padding: 0;
  font-family: 'Noto Sans KR', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html, body, #root {
  height: 100%;
  margin:0px;
  line-height: 1.5;
  background:#f9f9f9;
}

.library-title {
  padding-top:3px;
  padding-bottom : 3px;
  background: #ffffff;
  height : 50px;
  width :100%;
  position : relative;
}

.library-title h1 {
  font-weight: 600;
  left:100px;
  margin: 0;
  color:#170e42; 
  position : absolute;
  width : 50%;
}


.menu{
    display : grid;
    grid-template-columns: 1fr 1fr;
    grid-auto-rows: minmax(150px,auto);
    margin : 2% 15%;
    grid-gap: 0.5em;
    grid-template-areas: 
    "one four"
    "two five"
    "three six"
    "seven seven"
    "seven seven"
}

@media screen and (min-width : 760px){
  .menu{
    display : grid;
    grid-template-columns: 1fr 1fr 2fr;
    grid-auto-rows: minmax(175px,auto);
    margin : 2% 15% ;
    grid-gap: 0.5em;
    grid-template-areas: 
    "one four seven seven"
    "two five seven seven"
    "three six seven seven"
  }
}

.menu > div {
  padding:5px;
}

.card {
  height : 100%;
  width : 95%;
  margin : auto;
  border-radius: 2px;
  background-color:#ffffff;
  overflow:hidden;
}

.dot{
  height: 75px;
  width: 75px;
  background-color: rgb(228, 216, 180,0.5);
  border-radius: 50%;
  display: block;
  margin: 25px auto;
  position:relative;
}

.card:hover{
  cursor : pointer;
}

.ButtonCardI {
  height : 40%;
  width : 95%;
  margin : auto;
  border-radius: 2px;
  background-color:#E4D8B4;
  overflow:hidden;
  margin-top : 5%;
}

.ButtonCardR {
  height : 40%;
  width : 95%;
  margin : auto;
  border-radius: 2px;
  margin-top:2.5%;
  background-color:#E4D8B4;
  overflow:hidden;
  margin-bottom : 5%;
}

.backArrow {
  display: block;
  top: 20px;
  left: 15px;
  width: 50px;
  height: 53px;
  background-image: url(/static/media/arrow-back.a86975df.svg);
  color : #C2B28F;
  background-position: center;
  background-repeat: no-repeat;
  background-size: 28px;
  font-size: 0; 
  border: none;
  outline: none;
} 

.formPaper{
  background : #ffffff;
  width : 30%;
  padding-top : 10px;
  padding-bottom : 25px;
  padding-right:25px;
  padding-left:25px;
  margin : auto;
  margin-top : 5%;
}




.ReactTable{
    border:none;
    background-color: #ffffff;
    width: 90%;
    margin:auto;
    margin-top:5%;
}
.ReactTable .rt-thead .rt-tr{
    text-align:left;
}

.ReactTable .rt-thead.-header {
    box-shadow: none;
    color : #83B799;
}

.ReactTable .rt-thead .rt-th, .ReactTable .rt-thead .rt-td{
    padding : 15px 5px;
    border-right :none;
    box-shadow:none;
}

.ReactTable .rt-tbody .rt-td{
    border-right : none;
}
